

























import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import MathUtil from '@/typescript/MathUtil.ts';

@Component
export default class CMaxAndMin extends Vue {
  // Props
  @Prop() public maxTitle?: string;
  @Prop() public minTitle?: string;
  @Prop() public maxValue!: number;
  @Prop() public minValue!: number;

  // Data
  private dataMaxTitle: string = this.maxTitle !== undefined ? this.maxTitle: '最大値';
  private dataMinTitle: string = this.minTitle !== undefined ? this.minTitle: '最小値';
  private dataMaxValue: number = this.maxValue;
  private dataMinValue: number = this.minValue;

  // Watch
  @Watch('maxValue')
  private watchOnMaxValue(newValue: number, orgValue: number) {
    this.dataMaxValue = newValue;
  }
  @Watch('minValue')
  private watchOnMinValue(newValue: number, orgValue: number) {
    this.dataMinValue = newValue;
  }

  // Emit
  @Emit('inputMax')
  private emitInputMax(): number {
    return this.dataMaxValue;
  }
  @Emit('inputMin')
  private emitInputMin(): number {
    return this.dataMinValue;
  }

  // Methods
  private listenOnInputMaxValue($event: Event): void {
    let maxValue: string = (<HTMLInputElement>(<any>$event).target).value;
    this.dataMaxValue = Number(maxValue);
    if (!MathUtil.isRealNumber(maxValue) || Number(maxValue) < this.dataMinValue) {
      this.dataMaxValue = this.maxValue;
    }
    this.emitInputMax();
  }
  private listenOnInputMinValue($event: Event): void {
    let minValue: string = (<HTMLInputElement>(<any>$event).target).value;
    this.dataMinValue = Number(minValue);
    if (!MathUtil.isRealNumber(minValue) || this.dataMaxValue < Number(minValue)) {
      this.dataMinValue = this.minValue;
    }
    this.emitInputMin();
  }
}
