export default class MathUtil {
  //範囲指定で整数値をランダムに取得
  public static randomInt(min: number, max: number): number {
    let standardMax = max - min + 1;
    return min + Math.floor(Math.random() * standardMax);
  }

  //小数部分の桁数
  public static countDecimlPart(number: number): number {
    let decimalPartCount: number = 0;
    if (String(number).match(/(\.)([0-9]+)$/)) {
      let decimalPart: RegExpMatchArray | null = String(number).match(/(\.)([0-9]+)$/);
      if (decimalPart !== null) {
        decimalPartCount = decimalPart[2].length;
      }
    }
    return decimalPartCount;
  }

  //数字判定
  public static isRealNumber(number: string): boolean {
    return Boolean(number.match(/^(\+|\-)?([1-9][0-9]+|[0-9])(\.[0-9]+)?$/));
  }

  //整数判定
  public static isNaturalNumber(number: string): boolean {
    return Boolean(number.match(/^(\+|\-)?[1-9]?[0-9]+$/));
  }

  //自然数判定
  public static isNaturalNumberIncludingZero(number: string): boolean {
    return Boolean(number.match(/^[1-9]?[0-9]+$/));
  }

  //0埋め
  public static padZero(number: number, length: number): string {
    return ('0'.repeat(length) + String(number)).slice(-length);
  }

  //小数点指定位置
  public static toFixed(number: number, digitCount: number): string {
    return number.toFixed(digitCount);
  }

  //3桁毎のカンマ
  public static toLocaleString(number: number): string {
    return number.toLocaleString();
  }
}