import MathUtil from './MathUtil.ts';

export default class SentenceGen {
  public static readonly ALPHABET: string = 'abcdefghijklmnopqrstuvwxyz';
  public static readonly MAX_CHAR_COUNT: number = 12;
  public static readonly MIN_CHAR_COUNT: number = 1;
  public static readonly MAX_WORD_COUNT: number = 24;
  public static readonly MIN_WORD_COUNT: number = 1;
  public static readonly MAX_SENTENCE_COUNT: number = 16;
  public static readonly MIN_SENTENCE_COUNT: number = 1;
  public static readonly MAX_PARAGRAPH_COUNT: number = 4;
  public static readonly MIN_PARAGRAPH_COUNT: number = 1;
  private _baseString: string;
  private _maxCharCount: number;
  private _minCharCount: number;
  private _maxWordCount: number;
  private _minWordCount: number;
  private _maxSentenceCount: number;
  private _minSentenceCount: number;
  private _maxParagraphCount: number;
  private _minParagraphCount: number;

  public constructor() {
    this._baseString = SentenceGen.ALPHABET;
    this._maxCharCount = SentenceGen.MAX_CHAR_COUNT;
    this._minCharCount = SentenceGen.MIN_CHAR_COUNT;
    this._maxWordCount = SentenceGen.MAX_WORD_COUNT;
    this._minWordCount = SentenceGen.MIN_WORD_COUNT;
    this._maxSentenceCount = SentenceGen.MAX_SENTENCE_COUNT;
    this._minSentenceCount = SentenceGen.MIN_SENTENCE_COUNT;
    this._maxParagraphCount = SentenceGen.MAX_PARAGRAPH_COUNT;
    this._minParagraphCount = SentenceGen.MIN_PARAGRAPH_COUNT;
  }

  // Property
  public set baseString(string: string) {
    this._baseString = string;
  }
  public set maxCharCount(length: number) {
    this._maxCharCount = length;
  }
  public set minCharCount(length: number) {
    this._minCharCount = length;
  }
  public set maxWordCount(count: number) {
    this._maxWordCount = count;
  }
  public set minWordCount(count: number) {
    this._minWordCount = count;
  }
  public set maxSentenceCount(count: number) {
    this._maxSentenceCount = count;
  }
  public set minSentenceCount(count: number) {
    this._minSentenceCount = count;
  }
  public set maxParagraphCount(count: number) {
    this._maxParagraphCount = count;
  }
  public set minParagraphCount(count: number) {
    this._minParagraphCount = count;
  }

  // Method
  public randomChar(): string {
    return this._baseString.charAt(
      MathUtil.randomInt(0, this._baseString.length - 1)
    );
  }

  public randomWord(): string {
    let length: number = MathUtil.randomInt(this._minCharCount, this._maxCharCount);
    let word: string = '';
    for (let i: number = 0; i < length; i++) {
      word += this.randomChar();
    }
    return word;
  }

  public randomSentence(): string {
    let count: number = MathUtil.randomInt(this._minWordCount, this._maxWordCount);
    let words: string[] = [];
    for (let i: number = 0; i < count; i++) {
      words.push(this.randomWord());
    }
    let sentence: string = words.reduce((previousValue: string, currentValue: string): string => {
      let connector: string = MathUtil.randomInt(1, count) === 1 ? ', ' : ' ';
      return previousValue + connector + currentValue;
    })
    sentence = sentence.charAt(0).toUpperCase() + sentence.slice(1);
    let period: string;
    switch (MathUtil.randomInt(1, 12)) {
      case 1:
        period = '!';
        break;
      case 2:
        period = '?';
        break;
      default:
        period = '.'
    }
    return sentence + period;
  }

  public randomParagraph(): string {
    let count: number = MathUtil.randomInt(this._minSentenceCount, this._maxSentenceCount);
    let sentences: string[] = [];
    for (let i: number = 0; i < count; i++) {
      sentences.push(this.randomSentence());
    }
    return sentences.join(' ');
  }

  public randomSection(): string {
    let count: number = MathUtil.randomInt(this._minParagraphCount, this._maxParagraphCount);
    let paragraphs: string[] = [];
    for (let i: number = 0; i < count; i++) {
      paragraphs.push(this.randomParagraph());
    }
    return paragraphs.join('\n');
  }
}