











































































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CString from '@/components/links/CString.vue';
import CMaxAndMin from '@/components/CMaxAndMin.vue';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import SentenceGen from '@/typescript/SentenceGen.ts';

@Component({
  components: {
    CString,
    CMaxAndMin,
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: 'ランダム英文字列文',
    },
  },
})
export default class RandomSentence extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataMaxCharCountTitle: string = '最大文字数';
  private dataMinCharCountTitle: string = '最小文字数';
  private dataMaxWordCountTitle: string = '最大単語数';
  private dataMinWordCountTitle: string = '最小単語数';
  private dataMaxSentenceCountTitle: string = '最大の文数';
  private dataMinSentenceCountTitle: string = '最小の文数';
  private dataMaxParagraphCountTitle: string = '最大段落数';
  private dataMinParagraphCountTitle: string = '最小段落数';
  private dataButtonText: string = '出力';
  private dataMaxCharCount: number = SentenceGen.MAX_CHAR_COUNT;
  private dataMinCharCount: number = SentenceGen.MIN_CHAR_COUNT;
  private dataMaxWordCount: number = SentenceGen.MAX_WORD_COUNT;
  private dataMinWordCount: number = SentenceGen.MIN_WORD_COUNT;
  private dataMaxSentenceCount: number = SentenceGen.MAX_SENTENCE_COUNT;
  private dataMinSentenceCount: number = SentenceGen.MIN_SENTENCE_COUNT;
  private dataMaxParagraphCount: number = SentenceGen.MAX_PARAGRAPH_COUNT;
  private dataMinParagraphCount: number = SentenceGen.MIN_PARAGRAPH_COUNT;
  private dataText: string = '';
  private dataSamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'default',
      caption: 'デフォルト',
      content: [
        SentenceGen.MAX_CHAR_COUNT,
        SentenceGen.MIN_CHAR_COUNT,
        SentenceGen.MAX_WORD_COUNT,
        SentenceGen.MIN_WORD_COUNT,
        SentenceGen.MAX_SENTENCE_COUNT,
        SentenceGen.MIN_SENTENCE_COUNT,
        SentenceGen.MAX_PARAGRAPH_COUNT,
        SentenceGen.MIN_PARAGRAPH_COUNT,
      ].join(','),
    },
    {
      name: 'one_word',
      caption: '1単語',
      content: [12, 1, 1, 1, 1, 1, 1, 1].join(','),
    },
    {
      name: 'small',
      caption: '小さい文章',
      content: [4, 1, 8, 1, 4, 1, 1, 1].join(','),
    },
    {
      name: 'smaller',
      caption: '小さめの文章',
      content: [8, 1, 12, 1, 8, 1, 2, 1].join(','),
    },
    {
      name: 'bigger',
      caption: '大きめの文章',
      content: [12, 1, 32, 1, 16, 1, 8, 2].join(','),
    },
    {
      name: 'big',
      caption: '大きい文章',
      content: [12, 1, 64, 1, 16, 1, 16, 4].join(','),
    },
  ];
  private dataSampleIndex: number = 0;
  private dataCharCount: number = 0;
  private dataWordCount: number = 0;
  private dataSentenceCount: number = 0;
  private dataParagraphCount: number = 0;

  private created(): void {
  }

  // Watch
  @Watch('dataMaxCharCount')
  private watchOnMaxCharCount(newValue: number, orgValue: number) {
  }

  // Methods
  private listenOnSampleSelect($event: Event): void {
    this.dataSampleIndex = Number($event);
    let counts: number[] = this.dataSamples[this.dataSampleIndex].content.split(',')
      .map((value: string): number => {
        return Number(value);
      });
    if (1 < counts.length) {
      this.dataMaxCharCount = counts[0];
      this.dataMinCharCount = counts[1];
      this.dataMaxWordCount = counts[2];
      this.dataMinWordCount = counts[3];
      this.dataMaxSentenceCount = counts[4];
      this.dataMinSentenceCount = counts[5];
      this.dataMaxParagraphCount = counts[6];
      this.dataMinParagraphCount = counts[7];
    }
  }
  private inputMaxCharCount($event: Event): void {
    this.dataMaxCharCount = Math.floor(Number($event));
    if (this.dataMaxCharCount <= 0) {
      this.dataMaxCharCount = this.dataMinCharCount;
    }
  }
  private async inputMinCharCount($event: Event): Promise<any> {
    await Vue.nextTick();
    this.dataMinCharCount = Math.floor(Number($event));
    if (this.dataMinCharCount <= 0) {
      Vue.nextTick().then((): void => {
        this.dataMinCharCount = 1;
      });
    }
  }
  private inputMaxWordCount($event: Event): void {
    this.dataMaxWordCount = Math.floor(Number($event));
    if (this.dataMaxWordCount <= 0) {
      this.dataMaxWordCount = this.dataMinWordCount;
    }
  }
  private async inputMinWordCount($event: Event): Promise<any> {
    await Vue.nextTick();
    this.dataMinWordCount = Math.floor(Number($event));
    if (this.dataMinWordCount <= 0) {
      Vue.nextTick().then((): void => {
        this.dataMinWordCount = 1;
      });
    }
  }
  private inputMaxSentenceCount($event: Event): void {
    this.dataMaxSentenceCount = Math.floor(Number($event));
    if (this.dataMaxSentenceCount <= 0) {
      this.dataMaxSentenceCount = this.dataMinSentenceCount;
    }
  }
  private async inputMinSentenceCount($event: Event): Promise<any> {
    await Vue.nextTick();
    this.dataMinSentenceCount = Math.floor(Number($event));
    if (this.dataMinSentenceCount <= 0) {
      Vue.nextTick().then((): void => {
        this.dataMinSentenceCount = 1;
      });
    }
  }
  private inputMaxParagraphCount($event: Event): void {
    this.dataMaxParagraphCount = Math.floor(Number($event));
    if (this.dataMaxParagraphCount <= 0) {
      this.dataMaxParagraphCount = this.dataMinParagraphCount;
    }
  }
  private async inputMinParagraphCount($event: Event): Promise<any> {
    await Vue.nextTick();
    this.dataMinParagraphCount = Math.floor(Number($event));
    if (this.dataMinParagraphCount <= 0) {
      Vue.nextTick().then((): void => {
        this.dataMinParagraphCount = 1;
      });
    }
  }
  private generateRandomSentence(): void {
    let sentenceGen: any = new SentenceGen();
    sentenceGen.maxCharCount = this.dataMaxCharCount;
    sentenceGen.minCharCount = this.dataMinCharCount;
    sentenceGen.maxWordCount = this.dataMaxWordCount;
    sentenceGen.minWordCount = this.dataMinWordCount;
    sentenceGen.maxSentenceCount = this.dataMaxSentenceCount;
    sentenceGen.minSentenceCount = this.dataMinSentenceCount;
    sentenceGen.maxParagraphCount = this.dataMaxParagraphCount;
    sentenceGen.minParagraphCount = this.dataMinParagraphCount;
    this.dataText = sentenceGen.randomSection();
    this.count();
  }
  private count(): void {
    let chars: RegExpMatchArray | null = this.dataText.match(/[^ \n]/g);
    this.dataCharCount = chars !== null ? chars.length : 0;
    this.dataWordCount = this.dataText.split(/[\ \n]/).length;
    let periods: RegExpMatchArray | null = this.dataText.match(/[\.\?\!]/g);
    this.dataSentenceCount = periods !== null ? periods.length : 0;
    this.dataParagraphCount = this.dataText.split(/[\n]/).length;
  }
}
